import React from "react";
import Grid from "@material-ui/core/Grid";
import { Header } from "../../components/Header/Header";
import { HeaderLinks } from "../../components/Header/HeaderLinks";
import {
  AppBar,
  Tabs,
  Tab,
  makeStyles,
  Box,
  Typography,
} from "@material-ui/core";

export function Main() {
  const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
      backgroundColor: theme.palette.background.paper,
    },
  }));
  const [value, setValue] = React.useState(0);
  const classes = useStyles();
  const handleChange = (event: any, newValue: any) => {
    setValue(newValue);
  };
  return (
    <div>
      <Header
        brand="  "
        rightLinks={<HeaderLinks />}
        fixed
        color="white"
        changeColorOnScroll={{
          height: 200,
        }}
        changeOpacity={{
          backgroundColor: "rgba(0, 0, 0, 0.5)",
        }}
      />
      <div style={{ position: "relative", height: "900px" }}>
        <picture>
          <source srcSet={"./bg20.jpg"}></source>
          <img
            src={"./bg20.jpg"}
            width="100%"
            alt="20"
            style={{ maxWidth: "100%" }}
          ></img>
        </picture>
        <div
          style={{
            position: "absolute",
            top: "60%",
            left: "30%",
            transform: "translate( -50%, -50% )",

            color: "white",
            fontWeight: 400,
            fontSize: "52px",
            lineHeight: "1.25",
            width: "50%",
            backgroundColor: "rgba(0, 0, 0, 0.4)",
            fontFamily: "-apple-system, system-ui, BlinkMacSystemFont",
          }}
        >
          <div style={{ textAlign: "center" }}>
            <p>,  </p>
          </div>
          <div
            style={{
              textAlign: "center",
              lineHeight: "0.8",
              marginBottom: "40px",
              marginTop: "20px",
              fontWeight: 600,
            }}
          >
            <p
              style={{
                fontSize: "36px",
              }}
            >
                -   .   
                 .
            </p>
          </div>
        </div>
        <div style={{ height: "72px", backgroundColor: "white" }}></div>
        <Grid container style={{ background: "white" }}>
          <Grid item xs={2} md={2} lg={2}></Grid>
          <Grid item xs={8} md={8} lg={8}>
            <div>
              <h3>
                    : c    
                .
              </h3>
              <p style={{ fontSize: "18px" }}>
                        
                .    ,     
                .          ,
                     ,    .
              </p>{" "}
              <p style={{ fontSize: "18px" }}>
                 ,       
                ?      
                ,       .
                        .
              </p>
              <p style={{ fontSize: "18px" }}>
                     
                    .    
                  11-13%,     
                   .   
                :
              </p>
              <ul>
                <li> </li>
                <li> </li>
                <li>  -  </li>
                <li>
                        
                   
                </li>
              </ul>
            </div>
          </Grid>
          <Grid item xs={2} md={2} lg={2}></Grid>
        </Grid>
        <div style={{ height: "72px", backgroundColor: "white" }}></div>
        <picture>
          <source srcSet={"./bg21.jpg"}></source>
          <img
            src={"./bg21.jpg"}
            width="100%"
            alt="21"
            style={{ maxWidth: "100%" }}
          ></img>
        </picture>
        <div style={{ height: "72px", backgroundColor: "white" }}></div>
        <Grid container style={{ background: "white" }}>
          <Grid item xs={2} md={2} lg={2}></Grid>
          <Grid item xs={8} md={8} lg={8}>
            <div>
              <h3> </h3>
              <p>
                      
                   .     
                    :  
                 ,      
                    .
              </p>
              <h3>   </h3>
              <p>
                       
                  .    ,  
                       . 
                    ,   
                ,         .
                  ,   ,      
                  ?   ...
              </p>
              <p>
                       :
              </p>
            </div>
          </Grid>
          <Grid item xs={2} md={2} lg={2}></Grid>
        </Grid>
        <Grid container style={{ background: "white" }}>
          <Grid item xs={12} md={12} lg={12}>
            <div className={classes.root}>
              <AppBar position="static" style={{}}>
                <Tabs
                  style={{ background: "black" }}
                  value={value}
                  onChange={handleChange}
                  aria-label="simple tabs example"
                  centered
                >
                  <Tab label="Matte finish" {...a11yProps(0)} />
                  <Tab label="Satin finish" {...a11yProps(1)} />
                  <Tab label="High gloss finish" {...a11yProps(2)} />
                  <Tab label="Whitewash finish" {...a11yProps(3)} />
                  <Tab label="Dark finish (Walnut)" {...a11yProps(4)} />
                  <Tab label="Smooth finish" {...a11yProps(5)} />
                </Tabs>
              </AppBar>
            </div>
          </Grid>
        </Grid>
        <TabPanel value={value} index={0}>
          <picture>
            <source srcSet={"./bg24.jpg"}></source>
            <img
              src={"./bg24.jpg"}
              width="100%"
              alt="22"
              style={{ maxWidth: "100%" }}
            ></img>
          </picture>
        </TabPanel>
        <TabPanel value={value} index={1}>
          <picture>
            <source srcSet={"./bg25.jpg"}></source>
            <img
              src={"./bg25.jpg"}
              width="100%"
              alt="22"
              style={{ maxWidth: "100%" }}
            ></img>
          </picture>
        </TabPanel>
        <TabPanel value={value} index={2}>
          <picture>
            <source srcSet={"./bg26.jpg"}></source>
            <img
              src={"./bg26.jpg"}
              width="100%"
              alt="22"
              style={{ maxWidth: "100%" }}
            ></img>
          </picture>
        </TabPanel>
        <TabPanel value={value} index={3}>
          <picture>
            <source srcSet={"./bg27.jpg"}></source>
            <img
              src={"./bg27.jpg"}
              width="100%"
              alt="22"
              style={{ maxWidth: "100%" }}
            ></img>
          </picture>
        </TabPanel>
        <TabPanel value={value} index={4}>
          <picture>
            <source srcSet={"./bg28.jpg"}></source>
            <img
              src={"./bg28.jpg"}
              width="100%"
              alt="22"
              style={{ maxWidth: "100%" }}
            ></img>
          </picture>
        </TabPanel>
        <TabPanel value={value} index={5}>
          <picture>
            <source srcSet={"./bg29.jpg"}></source>
            <img
              src={"./bg29.jpg"}
              width="100%"
              alt="22"
              style={{ maxWidth: "100%" }}
            ></img>
          </picture>
        </TabPanel>
        <div style={{ height: "72px", backgroundColor: "white" }}></div>

        <Grid container style={{ background: "white" }}>
          <Grid item xs={2} md={2} lg={2}></Grid>
          <Grid item xs={4} md={4} lg={4}>
            <div style={{ display: "flex", alignContent: "center" }}>
              <p style={{ fontSize: "18px" }}>
                <h3 style={{ fontWeight: 600 }}></h3>
                      :    
                    .  
                    ,    . 
                      . 
                       
                .
              </p>
            </div>
          </Grid>
          <Grid item xs={4} md={4} lg={4}>
            <picture>
              <source srcSet={"./bg23.jpg"}></source>
              <img
                src={"./bg23.jpg"}
                alt="23"
                width="100%"
                style={{ maxWidth: "100%" }}
              ></img>
            </picture>
          </Grid>
          <Grid item xs={2} md={2} lg={2}></Grid>
        </Grid>
      </div>
    </div>
  );
}
function a11yProps(index: any) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`,
  };
}
function TabPanel(props: any) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}
